.nav-container {
  background-color: #1d1622;
  margin: 0 auto;
  max-width: 1600px;
  position: sticky;
  top: 0;
  z-index: 10;
}

nav {
  max-width: 1400px;
  margin: 0 auto;
 height: 60px;
}

.logoBtn {
  display: flex;
  padding: 5px 10px;
  border-bottom: 1px solid rgb(168, 168, 168);
  align-items: center;
  justify-content: space-between;
}

.logoBtn a {
  text-decoration: none;
  color: white;
  width: 35px;
  align-self: center;
  border-radius: 50%;
  opacity: 1;
  box-shadow: 0 0 8px 1px rgb(255, 255, 255);
}
.logoBtn a:hover {
  opacity: 0.8;
  box-shadow: 0 0 1px 3 px rgb(252, 250, 250);
}

.logoBtn img {
  width: 35px;
  display: block;
  border-radius: 50%;
  height: 40px;
  padding: 2px;
}

.bar1,
.bar2,
.bar3,
.bar4 {
  background-color: lightgray;
  width: 35px;
  padding: 1px;
  margin: 5px;
  transition: all 0.5s ease-in-out;
}

.animateBar.bar1 {
  transform: rotate(-45deg) translate(-5px, 5px);
  background: white;
  width: 30px;
}
.animateBar.bar2 {
  opacity: 0;
}
.animateBar.bar3 {
  transform: rotate(45deg) translate(-4px, -5px);
  background: white;
  width: 30px;
}

.btn1
 {
  cursor: pointer;
}

.links {
  width: 100%;
  transform: translate(-100%);
  position: fixed;
  top: 51px;
  bottom: 0;
  left: 0;
  right: 0;
  background: #1d1622;
  z-index: 10;
  opacity: 1;
  transition: 0.4s ease-in-out;
}

.new-links {
  width: 100%;
  transform: translate(0%);
  position: fixed;
  top: 51;
  bottom: 0;
  left: 0;
  right: 0;
}

.links li a {
  display: block;
  padding: 10px;
  color: lightgray;
  transition: all 0.3s ease-in-out;
  font-size: 19px;
  text-decoration: none;
  margin-top: 12px;
}
.links li a:hover {
 transform: scale(1.1);
  color: white;
}
.adminLi {
  display: none;
}


@media screen and (min-width: 780px) {
  .logoBtn {
    border: none;
    padding: 0;
  }

  .btn1 {
    display: none;
  }

  nav {
    max-width: 1450px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
  }
  .links {
    position: relative;
    display: flex;
    width: auto;
    top: auto;
    transform: translate(0%);
  }
  .new-links {
    width: 100%;
    transform: translate(0%);
    position: relative;
    top: auto;
  }

  .links li a {
    font-size: 15px;
    /* font-weight: 400; */
    padding:15px;
  }
  .links li a:hover {
  transform: scale(1.1);
    background: none;
  }
}

@media screen and (min-width: 1100px){
  
  .links li a {
    font-size: 16px;
    padding: 16px;
  }
}
